<template>
  <div class="xtx-goods-page" v-if="goods">
    <div class="wrapper">
      <!--  -->
      <xtx-bread>
        <xtx-bread-item to="/"></xtx-bread-item>
        <xtx-bread-item :to="`/category/${goods.categories[1].id}`">{{ goods.categories[1].name }}</xtx-bread-item>
        <xtx-bread-item :to="`/category/${goods.categories[0].id}`">{{ goods.categories[0].name }}</xtx-bread-item>
        <xtx-bread-item>{{ goods.name }}</xtx-bread-item>
      </xtx-bread>
      <!--  -->
      <div class="goods-info">
        <div class="media">
          <goods-image :images="goods.mainPictures"></goods-image>
          <goods-sales></goods-sales>
        </div>
        <div class="spec">
          <goods-name :goods="goods"></goods-name>
          <goods-sku :goods="goods" @change="changeSku"></goods-sku>
          <!--  -->
          <xtx-numbox label="" v-model="num" :max="goods.inventory"></xtx-numbox>
          <!--  -->
          <xtx-button @click="insertCart()" type="primary" size="large" style="margin-top: 20px"></xtx-button>
        </div>
      </div>
      <!--  -->
      <goods-relevant :goodsId="goods.id"></goods-relevant>
      <!--  -->
      <div class="goods-footer">
        <div class="goods-article">
          <!-- + -->
          <goods-tabs></goods-tabs>
          <!--  -->
          <goods-warn></goods-warn>
        </div>
        <!-- 24+ -->
        <div class="goods-aside">
          <goods-hot></goods-hot>
          <goods-hot :type="2"></goods-hot>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import GoodsImage from '@/views/Goods/components/GoodsImage.vue'
import GoodsSales from '@/views/Goods/components/GoodsSales.vue'
import GoodsName from '@/views/Goods/components/GoodsName.vue'
import GoodsSku from '@/views/Goods/components/GoodsSku.vue'
import GoodsRelevant from '@/views/Goods/components/GoodsRelevant.vue'
import GoodsTabs from '@/views/Goods/components/GoodsTabs.vue'
import GoodsWarn from '@/views/Goods/components/GoodsWarn.vue'
import GoodsHot from '@/views/Goods/components/GoodsHot.vue'
import { ElMessage } from 'element-plus'
import { nextTick, provide, ref, watch } from 'vue'
import { useRoute } from 'vue-router'
import { useStore } from 'vuex'
import { findGoods } from '@/api/product.js'
export default {
  name: 'GoodsInfo',
  components: {
    GoodsImage,
    GoodsSales,
    GoodsName,
    GoodsSku,
    GoodsRelevant,
    GoodsTabs,
    GoodsWarn,
    GoodsHot
  },
  setup () {
    const goods = useGoods()
    const changeSku = (sku) => {
      // 
      if (sku.skuId) {
        goods.value.price = sku.price
        goods.value.oldPrice = sku.oldPrice
        goods.value.inventory = sku.inventory
      }
      //  sku  {}
      currSku.value = sku
    }

    //  goods 
    provide('goods', goods)

    // 
    const num = ref(1)

    // 
    const store = useStore()
    const currSku = ref(null)
    const insertCart = () => {
      if (currSku.value && currSku.value.skuId) {
        const { skuId, specsText: attrsText, inventory: stock } = currSku.value
        const { id, name, price, mainPictures } = goods.value
        store.dispatch('cart/insertCart', {
          skuId,
          attrsText,
          stock,
          id,
          name,
          price,
          nowPrice: price,
          picture: mainPictures[0],
          selected: true,
          isEffective: true,
          count: num.value
        }).then(() => {
          ElMessage({
            type: 'success',
            message: ''
          })
        })
      } else {
        ElMessage({
          type: 'warning',
          message: ''
        })
      }
    }
    return { goods, changeSku, num, insertCart }
  }
}
// 
const useGoods = () => {
  // ID
  const goods = ref(null)
  const route = useRoute()
  watch(() => route.params.id, (newVal) => {
    if (newVal && `/product/${newVal}` === route.path) {
      findGoods(route.params.id).then(data => {
        // null v-if
        goods.value = null
        nextTick(() => {
          goods.value = data.result
        })
      })
    }
  }, { immediate: true })
  return goods
}
</script>

<style lang="less" scoped>
  .xtx-goods-page {
    background-color: #f5f5f5;
    padding-bottom: 40px;
    .goods-info {
      display: flex;
      min-height: 600px;
      background-color: #fff;
      .media {
        width: 580px;
        height: 600px;
        padding: 30px 50px;
      }
      .spec {
        flex: 1;
        padding: 30px 30px 30px 0;
      }
    }
    .goods-footer {
      display: flex;
      margin-top: 20px;
      .goods-article {
        width: 940px;
        margin-right: 20px;
      }
      .goods-aside {
        width: 280px;
        min-height: 1000px;
      }
    }
  }
</style>
